/* Основний блок сторінки */
.page {
    background: linear-gradient(to bottom right, #0B3B8E, #A3D2DB, #F0F0F0);
    padding: 20px;
    font-family: Arial, sans-serif;
    color: #272221;
  }
  
  /* Контент */
  .content {
    padding: 20px;
  
    &__title {
      color: #272221;
      margin-bottom: 20px;
    }
  
    &__text {
      font-size: 1.2em;
      line-height: 1.6;
    }
  }
  
  /* Навігація в хедері */
  .header {
    &__nav {
      display: flex;
    }
  
    &__nav-link {
      color: #3C5A94;
      text-decoration: none;
      padding: 10px;
      margin-right: 10px;
  
      &:hover {
        text-decoration: underline;
      }
    }
  }
  
  /* Таблиці */
  .table {
    width: 100%;
    border-collapse: collapse;
  
    &__cell {
      padding: 10px;
      text-align: left;
      border: 1px solid #3C5A94;
    }
  
    &__header {
      background-color: #3C5A94;
      color: #fff;
    }
  }
  
  /* Футер */
  .footer {
    text-align: center;
    padding: 20px;
    background-color: #3C5A94;
    color: #fff;
  
    &__link {
      color: #fff;
  
      &:hover {
        text-decoration: underline;
      }
    }
  }